import './AdminTipoVehiculoUpdate.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useRef, useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { AdminItemField, Page } from '../../components'
import { RUTAS, TIPOS_REFRIGERACION_SELECT } from '../../constants'
import { cambiar_formato_numero } from '../../helpers'
import {DetailViewProps, useToaster} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { TipoVehiculo } from '../../services/api'

export const AdminTipoVehiculoUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const [Loading, setLoading] = useState(false)
  const [Errors, setErrors] = useState<TipoVehiculo | null>()
  const [Datos, setDatos] = useState<TipoVehiculo | null>()
  const TipoVehiculoId = match.params.id
  const addToast = useToaster()

  const setup = () => {
    setDatos(null)
    if (TipoVehiculoId) {
      obtenerTipoVehiculo()
    }
  }

  React.useEffect(() => {
    setup()
  }, [TipoVehiculoId])

  const obtenerTipoVehiculo = () => {
    setLoading(true)
    rootStore.environment.api.adminGetTipoVehiculo(parseInt(TipoVehiculoId)).then((result) => {
      if (result.kind === 'ok') {
        setDatos(result.data)
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
      setLoading(false)
    })
  }

  const guardar = () => {
    if (Datos) {
      let datos = { ...Datos }
      if (datos.subida_bandera === undefined) {
        datos = { ...datos, subida_bandera: 0 }
      }
      if (datos.precio_minimo === undefined) {
        datos = { ...datos, precio_minimo: 0 }
      }
      if (datos.precio_km === undefined) {
        datos = { ...datos, precio_km: 0 }
      }
      if (datos.precio_minuto === undefined) {
        datos = { ...datos, precio_minuto: 0 }
      }
      if (datos.precio_parada === undefined) {
        datos = { ...datos, precio_parada: 0 }
      }
      if (datos.peso_maximo_kg) {
        datos = { ...datos, peso_maximo_kg: cambiar_formato_numero(datos.peso_maximo_kg) }
      }
      if (datos.espacio_alto_cm) {
        datos = { ...datos, espacio_alto_cm: cambiar_formato_numero(datos.espacio_alto_cm) }
      }
      if (datos.espacio_ancho_cm) {
        datos = { ...datos, espacio_ancho_cm: cambiar_formato_numero(datos.espacio_ancho_cm) }
      }
      if (datos.espacio_profundidad_cm) {
        datos = { ...datos, espacio_profundidad_cm: cambiar_formato_numero(datos.espacio_profundidad_cm) }
      }
      setLoading(true)
      setErrors(null)
      rootStore.environment.api.guardarTipoVehiculo(datos).then((result: any) => {
        setLoading(false)
        if (result.kind === 'ok') {
          history.push(RUTAS.ADMIN_TIPO_VEHICULO)
        } else if (result.kind === 'bad-data') {
          setErrors(result.errors)
          if (result.errors.non_field_errors) {
            addToast(result.errors.non_field_errors[0], 'warning')
          } else {
            addToast('Se han encontrado errores de validación', 'warning')
          }
        } else {
          addToast('Ocurrió un error al guardar vuelva a intentar en unos momentos', 'error')
        }
      })
    } else {
      addToast('Complete todos los campos para continuar', 'warning')
    }
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos as any as TipoVehiculo)
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">
                {Datos && Datos.id ? 'Editar tipo de vehículo' : 'Crear tipo de vehículo'}
              </h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push(RUTAS.ADMIN)}>Inicio</a>
                </li>
                <li className="breadcrumb-item">
                  <a onClick={() => history.push(RUTAS.ADMIN_TIPO_VEHICULO)}>Tipos de vehículo</a>
                </li>
                <li className="breadcrumb-item active">{Datos && Datos.id ? 'Editar' : 'Crear'}</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <h4 className={'mt-3 mb-4'}>
                <strong>Caracteristicas del tipo de vehículo</strong>
              </h4>
              <div className={'row'}>
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Nombre'}
                  tipo={'texto'}
                  campo={'nombre'}
                  valor={Datos ? Datos.nombre : ''}
                  error={Errors && Errors.nombre}
                  set_dato={setDatoInput}
                  columnas={3}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Refrigeración'}
                  tipo={'select2'}
                  campo={'refrigeracion'}
                  valor={Datos ? Datos.refrigeracion : 0}
                  error={Errors && Errors.refrigeracion}
                  set_dato={setDatoInput}
                  columnas={3}
                  opciones_select={TIPOS_REFRIGERACION_SELECT}
                  help_text={'Seleccionar tipo de refrigeración del tipo de vehículo'}
                />
              </div>
              <h4 className={'mt-3 mb-4'}>
                <strong>Precios</strong>
              </h4>
              <div className={'row'}>
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Subida de bandera'}
                  tipo={'numero'}
                  campo={'subida_bandera'}
                  valor={Datos ? Datos.subida_bandera : ''}
                  error={Errors && Errors.subida_bandera}
                  set_dato={setDatoInput}
                  columnas={3}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Precio mínimo'}
                  tipo={'numero'}
                  campo={'precio_minimo'}
                  valor={Datos ? Datos.precio_minimo : ''}
                  error={Errors && Errors.precio_minimo}
                  set_dato={setDatoInput}
                  columnas={3}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Precio por kilómetro'}
                  tipo={'numero'}
                  campo={'precio_km'}
                  valor={Datos ? Datos.precio_km : ''}
                  error={Errors && Errors.precio_km}
                  set_dato={setDatoInput}
                  columnas={3}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Precio por minuto'}
                  tipo={'numero'}
                  campo={'precio_minuto'}
                  valor={Datos ? Datos.precio_minuto : ''}
                  error={Errors && Errors.precio_minuto}
                  set_dato={setDatoInput}
                  columnas={3}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Precio por parada'}
                  tipo={'numero'}
                  campo={'precio_parada'}
                  valor={Datos ? Datos.precio_parada : ''}
                  error={Errors && Errors.precio_parada}
                  set_dato={setDatoInput}
                  columnas={3}
                />
              </div>
              <h4 className={'mt-3 mb-4'}>
                <strong>Datos para dimensiones de carga</strong>
              </h4>
              <div className={'row'}>
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Capacidad en kg'}
                  tipo={'decimales'}
                  campo={'peso_maximo_kg'}
                  valor={Datos ? Datos.peso_maximo_kg : 0}
                  error={Errors && Errors.peso_maximo_kg}
                  set_dato={setDatoInput}
                  columnas={3}
                  help_text={'Capacidad máxima de carga en kilogramos'}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Altura'}
                  tipo={'decimales'}
                  campo={'espacio_alto_cm'}
                  valor={Datos ? Datos.espacio_alto_cm : 0}
                  error={Errors && Errors.espacio_alto_cm}
                  set_dato={setDatoInput}
                  columnas={3}
                  help_text={'Altura de la zona de carga en centimetros'}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Ancho'}
                  tipo={'decimales'}
                  campo={'espacio_ancho_cm'}
                  valor={Datos ? Datos.espacio_ancho_cm : 0}
                  error={Errors && Errors.espacio_ancho_cm}
                  set_dato={setDatoInput}
                  columnas={3}
                  help_text={'Ancho de la zona de carga en centimetros'}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Profundidad'}
                  tipo={'decimales'}
                  campo={'espacio_profundidad_cm'}
                  valor={Datos ? Datos.espacio_profundidad_cm : 0}
                  error={Errors && Errors.espacio_profundidad_cm}
                  set_dato={setDatoInput}
                  columnas={3}
                  help_text={'Profundidad de la zona de carga en centimetros'}
                />
              </div>
              <div className={'form-group text-right'}>
                <a onClick={() => history.push(RUTAS.ADMIN_TIPO_VEHICULO)} className={'btn btn-light'}>
                  Cancelar
                </a>
                <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                  {Loading && <IonSpinner name="crescent" />}
                  {!Loading && <span>Guardar</span>}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminTipoVehiculoUpdate
